body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#searchbar {
  background-color: whitesmoke;
  color: black;
}

#searchbar::placeholder {
  color: grey;
}

#text-conatiner{
  width: 60%;
}

/* Icons --- Start */
.bi:hover {
  color: rgb(4, 184, 255) !important;
}

#icons {
  /* text-align: right; */
  align-items: flex-end;
  height: 50px;
  width: 60%;
  justify-content: right;
  align-items: flex-end;
  display: flex;
}

#heading{
  width: 20%;
}

#copyhandler,#clearText{
  background-color: white;
  border-color: white;
  border-style: none;
}

#input {
  display: block;
  align-items: center;
  display: flex;
}

body{
  background-color: white;
  color: black;

}

#input::placeholder {
  color: black;
  opacity: 80%;
}

#collapse {
  display: flex;
}

#ChangeCase,#Reverse {
  width: max-content;
}

#ChangeCaseContainer,#ReverseContainer{
  max-width: 30%;
}